import React from "react"
import styled from "styled-components"
import QF from "../components/quickfind"

const search = new QF(100)

var Box = styled.span`
  border: 1px solid black;
  height: 10px;
  width: 10px;
`

const rowConstruct = function(n, i) {
  var result = []
  for (var j = 0; j < n; j++) {
    result.push(<Box id={i * 10 + j} />)
  }
  return result
}

const boardConstruct = function(n) {
  var result = []
  for (var i = 0; i < n; i++) {
    result.push(<div>{rowConstruct(n, i)}</div>)
  }
  return result
}

const quicksearch = () => {
  const Section = styled.div`
    display: flex;
    flex-direction: row;
    @media (max-width: 820px) {
      margin-left: 1em;
      margin-right: 1em;
    }
  `

  return <Section>{boardConstruct(10)}</Section>
}

export default quicksearch
